body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  background-color: #f7fafc;
}

.app-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.app-title {
  text-align: center;
  font-size: 2.5rem;
  margin-bottom: 10px;
}

.user-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 20px;
}

.user-card {
  background: white;
  border-radius: 10px;
  box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
  overflow: hidden;
  transition: transform 0.2s;
}

.user-card:hover {
  transform: scale(1.05);
}

.user-card-header {
  padding: 16px;
  border-bottom: 1px solid #e2e8f0;
}

.user-name {
  font-size: 1.5rem;
  font-weight: bold;
  margin: 0;
}

.user-language {
  font-size: 1rem;
  color: #4a5568;
}

.user-card-body {
  padding: 16px;
}

.user-id {
  font-size: 1rem;
  color: #4a5568;
}

.user-bio {
  font-size: 0.9rem;
  color: #2d3748;
  margin-top: 8px;
}

.user-card-footer {
  padding: 16px;
  border-top: 1px solid #e2e8f0;
}

.user-version {
  font-size: 0.9rem;
  color: #a0aec0;
}

.loading-text {
  text-align: center;
  font-size: 1.2rem;
  color: #4a5568;
}

.end-message {
  text-align: center;
  margin: 20px 0;
  font-size: 1.2rem;
  color: #4a5568;
}

.filter-container {
  display: flex;
  align-items: center;
  margin: 20px 0;
  font-family: Arial, sans-serif;
}

.filter-container label {
  margin-right: 10px;
  font-size: 16px;
  color: #333;
}

.filter-container select {
  padding: 10px 15px;
  font-size: 16px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #fff;
  cursor: pointer;
  transition: border-color 0.3s;
}

.filter-container select:focus {
  border-color: #007bff;
  outline: none;
}

.filter-container select:hover {
  border-color: #007bff;
}
